import * as d3 from 'd3'

export default class MultiFieldMapViewD3 {
    constructor(el, props) {
        this.props = props;

        this.svg = d3.select(el).append('svg')
            .attr("width", props.width)
            .attr("height", props.height);

        this.svg.append("svg:image")
            .attr("x", 0)
            .attr("y", 0)
            .attr("src", "/data2/Sciebo/Promotion/EigenePaper/MultiFieldCorrelation/multi-field-correlations/Application/assets/test2.png")
            //.attr("href", "https://cdn.eso.org/images/thumb700x/eso1907a.jpg")


    }

    update(props) {
        this.props = props;
    }
}